import React, { useState, useEffect } from "react";
import "./AKeyboard.scss";
import { FontAwesomeIcon as Icon } from "@fortawesome/react-fontawesome";
import {
    faBackspace,
    faCheckCircle,
    faStepBackward,
    faFastBackward
} from "@fortawesome/free-solid-svg-icons";

const keyMap = {
    //first
    BracketLeft: ["", "]"],
    KeyP: ["", "P"],
    KeyO: ["", "O"],
    KeyI: ["", "I"],
    KeyU: ["", "U"],
    KeyY: ["", "Y"],
    KeyT: ["", "T"],
    KeyR: ["", "R"],
    KeyE: ["", "E"],
    KeyW: ["", "W"],
    KeyQ: ["", "Q"],

    //second
    Quote: ["", "'"],
    Semicolon: ["", ";"],
    KeyL: ["", "L"],
    KeyK: ["", "K"],
    KeyJ: ["", "J"],
    KeyH: ["", "H"],
    KeyG: ["", "G"],
    KeyF: ["", "F"],
    KeyD: ["", "D"],
    KeyS: ["", "S"],
    KeyA: ["", "A"],

    //Third
    BracketRight: ["", "["],
    Slash: ["", "/"],
    Period: ["", "."],
    Comma: ["", ","],
    KeyM: ["", "M"],
    KeyN: ["", "N"],
    KeyB: ["", "B"], //skip
    KeyV: ["", "V"],
    KeyC: ["", "C"],
    KeyX: ["", "X"],
    KeyZ: ["", "Z"], //??

    //Forth
    Backquote: ["", "`"],
    Space: [" ", "Space"],
    Backspace: [<Icon icon={faBackspace} />, "Bksp"],
    ClearWord: [<Icon icon={faStepBackward} />, "Ctrl+Bksp"],
    ClearAll: [<Icon icon={faFastBackward} />, "Ctrl+x"],
    Enter: [<Icon icon={faCheckCircle} />, "Enter"]
};

const keyRows = [
    [
        "BracketLeft",
        "KeyP",
        "KeyO",
        "KeyI",
        "KeyU",
        "KeyY",
        "KeyT",
        "KeyR",
        "KeyE",
        "KeyW",
        "KeyQ"
    ],
    [
        "Quote",
        "Semicolon",
        "KeyL",
        "KeyK",
        "KeyJ",
        "KeyH",
        "KeyG",
        "KeyF",
        "KeyD",
        "KeyS",
        "KeyA"
    ],
    [
        "BracketRight",
        "Slash",
        "Period",
        "Comma",
        "KeyM",
        "KeyN",
        "KeyB",
        "KeyV",
        "KeyC",
        "KeyX",
        "KeyZ"
    ],
    ["Enter", "Backspace", "ClearWord", "Space", "ClearAll", "Backquote"]
];

const AKeyboard = ({ initText, onUpdateText, onEnter, onCancel }) => {
    const [text, setText] = useState(initText);
    const [typedChar, setTypedChar] = useState("");

    const updateText = newText => {
        setText(newText);
        onUpdateText(newText);
    };

    const handleKeyDown = e => {
        const { code, ctrlKey } = e;
        setTypedChar(code);
        // setTimeout(() => {
        //     setTypedChar("");
        // }, 300);

        switch (code) {
            case "Escape":
                onCancel();
                break;
            case "Enter":
                onEnter(text);
                break;
            case "Backspace":
                if (ctrlKey) {
                    setTypedChar("ClearWord");
                    updateText(text.replace(/\S+\s*$/, ""));
                } else {
                    updateText(text.substr(0, text.length - 1));
                }
                break;
            case "ClearAll":
                updateText("");
                break;
            case "ClearWord":
                updateText(text.replace(/\S+\s*$/, ""));
                break;
            case "KeyX":
                if (ctrlKey) {
                    setTypedChar("ClearAll");
                    updateText("");
                    break;
                }
            default:
                if (keyMap[code]) {
                    updateText(text.concat(keyMap[code][0]));
                }
        }
    };

    useEffect(() => {
        document.addEventListener("keydown", handleKeyDown);
        return () => {
            document.removeEventListener("keydown", handleKeyDown);
        };
    }, [text, onEnter]);

    useEffect(() => {
        setText(initText);
    }, [initText]);

    return (
        <div id="AKeyboard">
            {keyRows.map((row, index) => (
                <div className="KeysRow" key={index}>
                    {row.map((key, index) => (
                        <span
                            key={index}
                            className={
                                "KeyButton " +
                                key +
                                (typedChar === key ? " typed" : "")
                            }
                            onClick={e => {
                                handleKeyDown({ code: key });
                            }}
                        >
                            <span className="KeyMainChar">
                                {keyMap[key][0]}
                            </span>
                            <span className="KeyLatinChar">
                                {keyMap[key][1]}
                            </span>
                        </span>
                    ))}
                </div>
            ))}
        </div>
    );
};

export default AKeyboard;
